.reviews {
  padding-top: 108px;
  padding-bottom: 121px;
  background-image: url(../images/reviews-bcg.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    position: relative;
    overflow: hidden;
  @include media("<=1440px") {
   
  }
    @include media("<=810px") {
      padding: 80px 15px 100px;
    }
}

.reviews__slider {
  padding-bottom: 20px;
  width: 100%;
  position: relative;
  overflow: hidden;
  @include media("<=810px") {
    
  }
  @include media("<=430px") {
    
  }

    .slick-list {
      padding: 0 !important;
      @include media("<=1440px") {
        padding: 0 !important;
      }
            @include media("<=1280px") {
              padding: 0 !important;
            }
            @include media("<=1080px") {
              padding: 0 !important;
            }
            @include media("<=810px") {
              
            }
            @include media("<=430px") {
             
            }
      @include media("<=430px") {}
    }
  
    .slick-track {
      display: flex;
      align-items: center;
      justify-content: space-around;
      @include media("<=430px") {
        
      }
    }
  
    .slick-slide {
      position: relative;
      cursor: pointer;
      transition: all 0.3s linear;
  
  
      @include media("<=430px") {}
    }
}



.reviews__item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  column-gap: 37px;
  row-gap: 30px;
  max-width: 750px;
  width: 100%;
  height: 362px;
  padding: 29px;
  color: #252432;
  border-radius: 10px;
    background: #FFF;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.10);
  transition: all 0.8s ease 0.2s;
  @include media("<=1440px") {
    
  }
  @include media("<=1080px") {
    
  }
  
  @include media("<=810px") {
    
  }
  @include media("<=600px") {
    padding: 16px;
    row-gap: 18px;
    justify-content: center;
    column-gap: 20px;
  }
  
  
  
}

.reviews__image {
  max-width: 132px;
  width: 100%;
  margin-left: 29px;
  @include media("<=810px") {
    flex: 0 1 30%;
  }
    @include media("<=600px") {
      margin-left: 0;
    }
  img {
    width: 100%;
    display: block;
  }
}

.reviews__title {
 
    font-size: 23px;
    text-align: left;
    width: 100%;
    margin-bottom: 8px;
    @include media("<=450px") {
      font-size: 20px;
    }
}

.reviews__subtitle {
  font-size: 16px;
  max-width: 370px;
  @include media("<=450px") {
    font-size: 14px;
  }
}

.reviews__author {
color: #00062B;
  font-weight: 500;
  line-height: 1.2;
  
  @include media("<=810px") {
   flex: 0 1 60%;
  }
    @include media("<=430px") {
      
    }
  span {
    font-weight: 700;
  }
  p {
    opacity: 0.6;
  }
}

.reviews__text {
  font-size: 16px;
  line-height: 1.4;
  font-style: italic;
  padding-top: 29px;
  position: relative;
  border-top: 1px solid #EBEBEB;
  flex: 0 1 100%;
  @include media("<=810px") {
    
  }
    @include media("<=600px") {
      font-size: 14px;
      padding-top: 18px;
    }
        @include media("<=380px") {
          font-size: 12px;
        }
        &::before {
         content: '';
         position: absolute;
         top: -8px;
         left: 79px;
         border-top: 1px solid #EBEBEB;
          border-left: 1px solid #EBEBEB;
          background: #FFF;
          width: 15.55px;
            height: 15.55px;
            transform: rotate(45deg);
        }
        p {
          max-width: 630px;
          margin: 0 auto;
          display: block;
        }
}

