.info {
    padding: 75px 0 92px;
    @include media("<=810px") {
        padding-top: 0;
        padding-bottom: 40px;
    }
}

.info__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 42px;
    @include media("<=810px") {
        row-gap: 10px;
        flex-wrap: wrap;
    }
        @include media("<=500px") {
           flex-direction: column;
           align-items: center;
        }
}

.info__content {
    max-width: 497px;
}

.info__title {
    color: #C22821;
    font-size: 16px;
    margin-bottom: 8px;
    line-height: 1.2;
    @include media("<=810px") {
      text-align: center;
    }
}

.info__text {
    color: #0E69AF;
    font-size: 25px;
    line-height: 1.2;
    @include media("<=810px") {
        text-align: center;
        font-size: 24px;
    }
}

.info__image {
   
    max-width: 485px;
    width: 100%;
    aspect-ratio: 485 / 223;
@include media("<=810px") {
    display: none;
}
}