.about {
  padding: 72px 0 27px;
}

.about__inner {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  @include media("<=900px") {
    flex-direction: column;
    align-items: center;
  }
}

.about__image {
  flex: 0 1 100%;
  box-shadow: 4px 5px 6.8px 0px rgba(0, 0, 0, 0.25);
  max-width: 464px;
  margin-right: -30px;
  position: relative;
  @include media("<=1080px") {
    max-width: 420px;
  }
    @include media("<=900px") {
      margin-right: 0;
      margin-bottom: -40px;
    }
    @include media("<=500px") {
      max-width: 297px;
    }
  img {
    width: 100%;
    display: block;
  }
}
  
  .about__text {
    margin-top: 90px;
    margin-left: -30px;
    flex: 0 1 100%;
     max-width: 680px;
     padding: 100px 27px 63px 92px;
    background-image: url(../images/about-bcg.jpg);
      background-position: center top;
      background-repeat: no-repeat;
      background-size: cover;
      font-size: 20px;
      font-weight: 600;
      line-height: 22px;
      color: #fff;
      @include media("<=1080px") {
        font-size: 18px;
      }
        @include media("<=900px") {
          margin-top: 0;
          margin-left: 0;
          padding: 70px 30px 50px;
          text-align: center;
        }
            @include media("<=430px") {
              padding: 60px 20px 40px;
            }
  }

.calculator {
  padding-bottom: 51px;
  .section__title {
    color: #FFF;
    font-weight: 700;
  }
}

.calculator__inner {
  padding-top: 40px;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  @include media("<=1000px") {
   flex-direction: column;
   row-gap: 40px;
  }
}

.range {
  max-width: 690px;
  width: 100%;
  padding: 40px 20px;
  border-radius: 20px;
    background: #FFF;
    box-shadow: 0px 3.259px 10.348px 0px rgba(0, 0, 0, 0.03), 0px 15.407px 40.452px 0px rgba(0, 0, 0, 0.05), 0px 40px 127px 0px rgba(0, 0, 0, 0.08);
}

.range__title {
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
  font-size: 20px;
  font-weight: 500;
}

.range__sum {
  background: linear-gradient(90deg, #C91C04 0%, #D81E05 33.33%, #4B0900 66.67%, #4B0900 100%);
  margin: 50px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  column-gap: 15px;
 color: #FFF;
 border-radius: 30px;
 max-width: 496px;
@include media("<=430px") {
  column-gap: 10px;
  padding: 16px;

}
 span {
  font-size: 20px;
  font-weight: 500;
  @include media("<=430px") {
    font-size: 16px;
  }
 }
}

.sum {
  font-size: 44px;
    font-weight: 500;
    @include media("<=430px") {
      font-size: 28px;
    }
}

.irs {
  margin: 5px 0;
}

.irs--big .irs-single {
  color: white;
  text-shadow: none;
  padding: 4px 5px;
  
  border-radius: 4px;
    background: #D81E05;
}

.irs--big .irs-handle {
  top: 25px;
  width: 24px;
  height: 24px;
  border-radius: 24px;
    border: 4px solid #D81E05;
    background: #FFF;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 255, 0.30);
}

.irs--big .irs-bar {
  top: 33px;
  height: 12px;
  border-radius: 4px 0px 0px 4px;
    background: linear-gradient(90deg, #D81E05 0%, #C91C04 33.33%, #C91C04 66.67%, #D81E05 100%);
  border: none;  
}

.calculator__content {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
}

.calculator__info {
  color: #FFF;
  font-weight: 500;
  @include media("<=1000px") {
    text-align: center;
  }
  p {
    font-size: 16px;
    margin-bottom: 16px;
  }
  span {
    display: block;
    font-size: 42px;
  }
}