.hero {
  padding-bottom: 90px;
  position: relative;
//  z-index: 2;
  overflow-x: clip;
  @include media("<=1080px") {
    
  }
  @include media("<=810px") {
    padding-bottom: 60px;
  }
  @include media("<=430px") {
    padding-top: 19px;
  }
  
}

.hero__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include media("<=1200px") {
    justify-content: flex-end;
  }
  @include media("<=810px") {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
  }
  @include media("<=430px") {
    
  }
  @include media("<=375px") {
  }
  @include media("<=320px") {
  }
}

.hero__content {
  flex: 0 1 50%;
  z-index: 24;
  display: flex;
  align-items: center;
  justify-content: center;
  @include media("<=1440px") {
    padding-left: 15px;
  }
  @include media("<=1080px") {
   
    
  }
  @include media("<=810px") {
    align-items: center;
    text-align: center;
    max-width: 560px;
    padding-left: 0;
    margin-bottom: 53px;
  }
    @include media("<=430px") {
      margin-bottom: 10px;
    }
}

.hero__image {
  flex: 0 1 50%;
  position: relative;
  opacity: 0;
  transition: all 0.8s ease 0.6s;

  &._active {
    opacity: 1;
  }

  @include media("<=1440px") {
    
  }

  @include media("<=1200px") {
    max-width: 600px;
    flex: 0 1 100%;
  }

  @include media("<=810px") {
    max-width: 560px;
    margin-top: 0;
    margin-right: 0;
    width: 100%;
    aspect-ratio: 36 / 19;
    background: #D9D9D9;
    margin-bottom: 54px;
  }

  @include media("<=430px") {
    max-width: 380px;
  }

  @include media("<=350px") {}

  img {
    width: 100%;
    display: block;
    @include media("<=810px") {
      display: none;
    }
  }

}

.hero__title {
  font-size: 28px;
  color: #D81E05;
  width: 100%;
  line-height: 1.8;
  max-width: 467px;
  opacity: 0;
  transition: all 0.8s ease 0.2s;
  
    &._active {
      opacity: 1;
    }
        @include media("<=1440px") {
         
        }
  @include media("<=1280px") {
    
  }

  @include media("<=1200px") {
    font-size: 26px;
  }
  @include media("<=810px") {
    text-align: center;
    line-height: 1.2;
    
  }
  @include media("<=550px") {
    font-size: 24px;
  }

  @include media("<=380px") {
    font-size: 20px;
  }
}

.button {
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  padding: 14px;
  max-width: 300px;
  width: 100%;
  border: none;
  border-radius: 4px;
    background: #D81E05;
  transition: all ease-in-out 0.3s;
  position: relative;
 
  cursor: pointer;
  &:hover {
    background-color: #790404;
    color: #fff;
  }
    
        
    
       
  
}

.register {
  max-width: 354px;
  width: 100%;
  padding: 52px 20px 45px;
  margin: 0 auto;
  border-radius: 15px;
    background: rgba(74, 74, 74, 0.82);
    box-shadow: 0px 0px 12px 12px rgba(216, 30, 5, 0.29);
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 11px;
    position: absolute;
    top: 50%;
    right: 10%;
    transform: translateY(-50%);
    @include media("<=1200px") {
   right: 8%;
    }
        @include media("<=810px") {
          position: relative;
          top: auto;
          right: auto;
          transform: none;
          border-radius: 15px;
            background: #FFF;
            box-shadow: 0px 4px 19.3px 4px rgba(0, 0, 0, 0.19);
        }
}

.form__input {
  max-width: 300px;
  width: 100%;
  input {
  padding: 11px 12px;
  border-radius: 4px;
    background: #F7F7F7;
    border: none;
    max-width: 300px;
    width: 100%;
    color: #818181;
    font-size: 16px;
    text-transform: uppercase;
  }
}

.error-message {
  color: red;
  font-size: 0.9em;
}






