}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
  
}

.section__title {
  text-align: center;
  color: rgba(0, 0, 0, 0.80);
    font-size: 37px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.6;
    @include media("<=810px") {
      font-size: 40px;
    }
        @include media("<=430px") {
          font-size: 24px;
        }
}

.wrapper {
  background: linear-gradient(180deg, #D81E05 0%, #490900 100%);
}

//BURGER BUTTON//
.burger__button {
  display: none;

  @include media("<=810px") {
    display: block;
    position: fixed;
    top: 34px;
    right: 40px;
    z-index: 200;
  }

  @include media("<=430px") {
    top: 34px;
    right: 20px;
  }

  @include media("<=810px") {
    &.active {
      position: fixed;
      top: 38px;
      right: 40px;
      z-index: 200;
    }

    @include media("<=430px") {
      &.active {
        top: 36px;
        right: 20px;
      }
    }
  }

  .line {
    height: 4px;
    width: 50px;
    margin-bottom: 6px;
    background-color: #000;
    transition: all 200ms ease-in-out;
    opacity: 1;

    @include media("<=736px") {
      height: 4px;
      width: 48px;
    }
  }

  &.active {
    .line {
      background-color: #fff;

      &.one {
        transform: rotate(45deg) ;
      }

      &.two {
        transform: rotate(-45deg)  translateY(-7px) translateX(7px);

        @include media("<=736px") {
          transform: rotate(-45deg)  translateY(-7px) translateX(7px);
        }
      }

      &.three {
        opacity: 0;
      }
    }
  }
}

