.footer {
    border-top: 1px #CC4238 solid;
    padding: 36px 0 68px;
    @include media("<=650px") {
padding: 30px 0 40px;
border: none;
    }
}

.footer__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include media("<=650px") {
        flex-direction: column-reverse;
        align-items: center;
row-gap: 30px;
    }
}

.footer__nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    column-gap: 20px;
    row-gap: 20px;
    max-width: 818px;
    @include media("<=650px") {
        flex-direction: column;

    }
}

.footer__nav-link {
    color: #000;
    font-size: 12px;
    text-decoration: none;
    &:hover {
        text-decoration: underline;
    }
}
