.header {
  width: 100%;
  position: relative;
  padding: 42px 0 48px;
  position: relative;
    z-index: 30;
  @include media("<=1080px") {
    padding: 32px 0 38px;
  }
  @include media("<=810px") {
    padding: 24px 0;
    
  }
  @include media("<=430px") {
    
  }
 
}

.header__inner {
  
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  
  @include media("<=1080px") {
  
      margin: 0 auto;
    }
  @include media("<=810px") {
    column-gap: 0;
    justify-content: space-between;
  }
}

.header__nav {
  flex: 0 1 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 28px;
  @include media("<=1080px") {
    
  }

  @include media("<=810px") {
    display: none;
    max-width: 100%;

    &.active {
      display: flex;
      position: fixed;
      height: 50%;
      width: 100%;
      max-width: none;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      background-color: #D81E05;
      z-index: 20;
      margin-right: 0;
      margin-left: 0;
      padding-top: 20px;
      padding-bottom: 20px;
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      justify-content: center;
      align-content: space-between;
      align-items: center;
      opacity: 1;

      @include media("<=430px") {
        padding-bottom: 0;

      }

      .header__link {
        padding: 15px;

        @include media("<=430px") {
          padding: 10px;
        }

        @include media("<=350px") {
          padding: 10px;
        }
      }
    }
  }
}

.header__link {
  font-size: 20px;
  font-weight: 400;
  text-decoration: none;
  color: #000;
  transition: all 0.3s ease;
  position: relative;

  @include media("<=810px") {
    color: #fff;
  }

  &:hover {
    color: #D81E05;
  }
}

.header__link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.1em;
  background: #000;
  bottom: -6px;
  left: 0;
  transform: scale(0, 1);
  transition: transform 0.3s ease;
}

.header__link:hover::after {
  transform: scale(1, 1);
}

.logo {
  display: block;
  max-width: 79px;
  width: 100%;
  flex: 0 1 100%;
  position: relative;
  @include media("<=1440px") {
    
  
  }
    @include media("<=1080px") {
      
     
    }
  @include media("<=810px") {
    position: relative;
    z-index: 21;
    
    
  }
    @include media("<=430px") {
      
    }
  &-header {
    position: absolute;
    top: 27px;
    left: 58px;
    @include media("<=810px") {
        position: relative;
        z-index: 21;
    top: auto;
    left: auto;
    
      }
  }  
  img {
    width: 100%;
    display: block;
  }
}

