.contacts {
    overflow: hidden;
    padding: 467px 0 0;
    margin-top: -457px;
    background-image: url(../images/wrapper-bcg.png);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: cover;
    position: relative;
    @include media("<=810px") {
        margin-top: -680px;
    }
    @include media("<=430px") {
        margin-top: -600px;
    }

}

.contacts__inner {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    column-gap: 150px;

    @include media("<=1440px") {
        column-gap: 105px;
    }

    @include media("<=1080px") {
        column-gap: 60px;
    }

    @include media("<=810px") {
        align-items: center;
        column-gap: 15px;
        justify-content: space-around;
    }

    @include media("<=430px") {}

    @include media("<=650px") {
        flex-direction: column;
        align-items: center;
    }
}

.contacts__content {
    padding-top: 140px;
    display: flex;
    flex-direction: column;
    max-width: 519px;
    width: 100%;
    justify-content: center;

    @include media("<=1440px") {
        max-width: 448px;
        padding-top: 120px;
    }

    @include media("<=1080px") {
        padding-top: 75px;
    }

    @include media("<=810px") {
        padding-top: 0;
        max-width: 340px;
    }

    @include media("<=430px") {}

    @include media("<=650px") {
        align-items: center;
        text-align: center;
        margin-bottom: 5px;
    }
}

.contacts__title {
    font-family: 'Integral CF';
    font-size: 56px;
    font-weight: 500;
    margin-bottom: 24px;

    @include media("<=1440px") {
        font-size: 48px;
    }

    @include media("<=810px") {
        font-size: 36px;
    }

    @include media("<=500px") {
        font-size: 24px;
    }

    @include media("<=350px") {
        font-size: 16px;
    }
}

.contacts__text {
    font-size: 28px;
    margin-bottom: 60px;
    font-weight: 500;

    @include media("<=1440px") {
        font-size: 24px;
        margin-bottom: 40px;
    }

    @include media("<=810px") {
        font-size: 20px;
    }

    @include media("<=500px") {
        font-size: 14px;
    }
}

.contacts__image {
    max-width: 758px;
    width: 100%;

    @include media("<=1440px") {
        
    }

    @include media("<=1080px") {
       
    }

    @include media("<=810px") {
      
    }

    @include media("<=430px") {
        
    }

    @include media("<=375px") {
        
    }

    img {
        display: block;
        width: 100%;
    }
}