.features {
  position: relative;
  padding-bottom: 67px;
  overflow: hidden;
  
  @include media("<=1440px") {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  
}

.features__inner {
  padding-top: 48px;
  display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 3;
    @include media("<=1440px") {
      
      margin: 0 auto;
    }
        @include media("<=1080px") {
          
        }
                @include media("<=900px") {
                  flex-direction: column;
                  row-gap: 44px;
                }
        @include media("<=500px") {
         
        }
}

.features__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 395px;
  flex: 0 1 33%;
  padding: 10px 31px;
  text-align: left;
    border-right: 1px solid #EE1B25;
  min-height: 370px;
  transition: all 0.8s ease 0.2s;
  @include media("<=1440px") {
    
  }
  @include media("<=1080px") {
    padding: 10px 24px;
  }
  
  @include media("<=900px") {
      align-items: center;
      text-align: center;
      border: none;
      flex: 0 1 100%;
      min-height: auto;
  }
  @include media("<=500px") {
    
  }
 @include media("<=360px") {
   
   
 }
  &:last-of-type {
    border: none;
  }
}

 .features__icon {
   max-width: 95px;
   width: 100%;
   margin-bottom: 30px;
   flex: 1 0 auto;
   @include media("<=1200px") {
     
     max-width: 80px;
   }

   @include media("<=810px") {
     flex: 0 1 auto;
     margin-bottom: 20px;
   }

   @include media("<=380px") {
     
   }

   img {
     width: 100%;
     display: block;
   }
 }

.features__title {
  font-size: 31px;
  margin-bottom: 34px;
  width: max-content;

  @include media("<=1280px") {
    font-size: 28px;
  }
@include media("<=1080px") {
  font-size: 24px;
}
  @include media("<=810px") {
    margin-bottom: 24px;
  }
    @include media("<=410px") {
      font-size: 18px;
    }
}

.features__text {
  color: #242424;
    font-size: 16px;
    line-height: 140%;
    margin-bottom: 35px;
@include media("<=1440px") {
    font-size: 16px;
  }
  @include media("<=900px") {
    
margin-bottom: 24px;
  }

  @include media("<=430px") {
    font-size: 14px;
  }
}

.features__button {
  max-width: 360px;
  width: 100%;
  display: flex;
  align-items: center;
    justify-content: center;
    position: relative;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    line-height: 1.2;
    color: #fff;
    border-radius: 15px;
      background: #D21D05;
      box-shadow: 3px 4px 10px 0px rgba(0, 0, 0, 0.25);
      padding: 15px;
      border: 1px #D21D05 solid;
      transition: all 0.3s ease;
      &:hover {
        background-color: transparent;
        color: #D21D05;
      }
}

.partners {
  overflow: hidden;
  position: relative;
  padding: 72px 0 39px;
          @include media("<=1440px") {
            
          }
    @include media("<=810px") {
        padding-bottom: 60px;
        
      }
    @include media("<=430px") {
      
    }
}



.partners__inner {
  padding-top: 59px;
  display: flex;
  align-items: center;
  justify-content: center;

  
  @include media("<=1080px") {
    column-gap: 40px;
  }
    @include media("<=810px") {
      flex-wrap: wrap;
      justify-content: space-around;
      row-gap: 12px;
    }
        @include media("<=430px") {
          column-gap: 30px;
        }
}

.partners__item {
 width: 100%;
 max-width: 660px;
  @include media("<=1080px") {
    
  }
    @include media("<=430px") {
      
    }
  img {
    width: 100%;
    display: block;
  }
}